<script>
import TimeLine from "./components/TimeLine.vue";
import ShowDemos from "./components/ShowDemos.vue";

export default {
  name: "app",
  components: {
    TimeLine,
    ShowDemos
  },
  data() {
    return {
      currentHeight: null,
      ishow: false
    };
  },
  // mounted destroyed 生命钩子 监听滚动事件
  mounted() {
    window.addEventListener("scroll", this.scrollToTop);
  },
  destroyed() {
    window.removeEventListener("scroll", this.scrollToTop);
  },

  methods: {
    scrollToTop() {
      this.currentHeight =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      this.ishow = this.currentHeight > 60 ? true : false;
    }
  }
};
</script>

